body {
  margin          : 0;
  padding         : 0;
  overflow        : hidden;
  background-color: #080808;
  width           : 100vw;
  height          : 100vh;
}

* {
  touch-action    : none;
  -ms-touch-action: none;
}

.board {
  position: absolute;
  top     : 50%;
  left    : 50%;
  height  : 0;
  width   : 0;
  margin  : 0;
  padding : 0;
  overflow: visible;
}

.space {
  outline         : 1px solid #242424;
  outline-offset  : -1px;
  background-color: #121212;
  border          : none;
  padding         : 0;
  margin          : 0;
  -webkit-transition      : background-color 0.5s;
  -o-transition      : background-color 0.5s;
  transition      : background-color 0.5s;
}

.space:hover {
  background-color: #242424;
}

.chunk-container {
  display: block;
}

.player-icon {
  display: block;
  padding: 5px;
}

#zoom-bar {
  position: absolute;
  top     : 10px;
  right   : 5px;
  z-index : 99;
  display : block;
}

#zoom-bar button {
  display         : block;
  width           : 30px;
  height          : 30px;
  margin          : 5px;
  background-color: white;
  border          : 1px solid black;
  color           : black;
  -webkit-transition      : all 0.1s ease-in-out;
  -o-transition      : all 0.1s ease-in-out;
  transition      : all 0.1s ease-in-out;
}

#zoom-bar button:hover {
  background-color: black;
  color           : white;
  border-color    : white;
}

.win-square {
  outline       : 4px solid white;
  outline-offset: -2px;
  z-index       : 99;
  -webkit-transition    : border 0.5s;
  -o-transition    : border 0.5s;
  transition    : border 0.5s;
}

.chunk {
  display: block;
}

.chunk::before {
  position  : absolute;
  top       : 0;
  bottom    : 0;
  left      : 0;
  right     : 0;
  content   : "";
  z-index   : -1;
  -webkit-box-shadow: #242424 5px 5px, #242424 -5px 5px, #242424 5px -5px, #242424 -5px -5px;
          box-shadow: #242424 5px 5px, #242424 -5px 5px, #242424 5px -5px, #242424 -5px -5px;
}

#limit-dialog {
  position        : absolute;
  display         : block;
  top             : 15px;
  left            : 10px;
  font-size       : 1rem;
  font-family     : 'Iosevka', monospace;
  padding         : 10px;
  border          : 1px solid black;
  border-radius   : 0;
  background-color: white;
  color           : black;
  z-index         : 99;
}

#score-dialog {
  position        : absolute;
  display         : block;
  bottom          : 10px;
  left            : 10px;
  font-size       : 1rem;
  font-family     : 'Iosevka', monospace;
  border          : 1px solid black;
  padding         : 10px;
  border-radius   : 0;
  background-color: white;
  color           : black;
  z-index         : 99;
}

#player-bar {
  position  : absolute;
  display   : block;
  z-index   : 99;
  top       : 0;
  left      : 0;
  width     : 100%;
  height    : 5px;
  -webkit-transition: all 0.1s;
  -o-transition: all 0.1s;
  transition: all 0.1s;
}

.menu {
  background-color: white;
  display         : grid;
  -ms-flex-pack : center;
      justify-content : center;
}

.menu-item {
  display         : block;
  padding         : 5px;
  background-color: #888;
}


